import { DomElement } from '../lib/dom/domElement';

export class Panel extends DomElement<'div'> {
    constructor(public readonly id: string, public readonly name: string) {
        super('div', {
            className: `panel`,
            id
        })
        this.child('div', {
            text: name,
            style: {
                'color': 'white'
            }
        })
    }
}